async function sendNewArticle(title, content, category) {
  const time = Date.now();
  return await fetch(
    'https://api.github.com/repos/xiaozhaoqi/privateRepository/contents/files/article/' +
    title + '-' + time +
    '.md?access_token=' +
    leftToken +
    rightToken,
    {
      method: 'PUT',
      body: JSON.stringify({
        message: 'AutoPush Article: ' + title,
        // @ts-ignore
        content: btoa(
          unescape(
            encodeURIComponent(
              JSON.stringify({
                title: title,
                content: content,
                time: time,
                category: category
              })
            )
          )
        ),
      }),
    }
  )
    .then(res => res.json())
    .then(res => {
      return true;
    })
    .catch(err => {
      alert('åˆ›å»ºè®°å½•å¤±è´¥ï¼Œè¯·æ£€æŸ¥ç½‘ç»œè¿æ¥ã€‚')
      return false;
    });
}
async function removeArticle(title, sha) {
  return await fetch(
    'https://api.github.com/repos/xiaozhaoqi/privateRepository/contents/files/article/' +
    title +
    '.md?access_token=' +
    leftToken +
    rightToken,
    {
      method: 'DELETE',
      body: JSON.stringify({
        message: 'AutoDelete article: ' + title,
        // @ts-ignore
        sha: sha,
      }),
    }
  )
    .then(res => res.json())
    .then(res => {
      return res;
    })
    .catch(err => {
      console.log(err);
    });
}
